import {
  SIGNUPLOGIN_SUCCESS,
  SIGNUPLOGIN_FAIL,
  LOAD_USER_SUCCESS,
  LOAD_USER_FAIL,
  USER_LOGOUT,
} from "../types";

const initialState = {
  user: null,
  token: localStorage.getItem("token"),
  isAuthenticated: false,
};

const authFunction = (state = initialState, action) => {
  switch (action.type) {
    case SIGNUPLOGIN_SUCCESS:
      localStorage.setItem("token", action.payload.token);
      return {
        ...state,
        token: action.payload,
        isAuthenticated: true,
      };
    case LOAD_USER_SUCCESS:
      return {
        ...state,
        user: action.payload,
      };
    case SIGNUPLOGIN_FAIL:
    case LOAD_USER_FAIL:
    case USER_LOGOUT:
      localStorage.removeItem("token");
      return {
        ...state,
        user: null,
        token: null,
        isAuthenticated: false,
      };

    default:
      return state;
  }
};

export default authFunction;
