import axios from "axios";
import {
  SIGNUPLOGIN_SUCCESS,
  SIGNUPLOGIN_FAIL,
  LOAD_USER_SUCCESS,
  LOAD_USER_FAIL,
  USER_LOGOUT,
} from "../types";

import setAuthtoken from "../../utils/setAuthToken";

export const loadUser = () => async (dispatch) => {
  if (localStorage.token) {
    setAuthtoken(localStorage.token);
  }

  try {
    const res = await axios.get(
      "http://localhost:5000/api/v1/users/getLoginUser"
    );
    dispatch({
      type: LOAD_USER_SUCCESS,
      payload: res.data.user,
    });
  } catch (err) {
    console.error(err);
    dispatch({ type: LOAD_USER_FAIL });
  }
};

//==== Login a User
export const loginUser = (email, password) => async (dispatch) => {
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };
  const body = JSON.stringify({ email, password });

  try {
    const res = await axios.post(
      "http://localhost:5000/api/v1/users/login",
      body,
      config
    );
    dispatch({
      type: SIGNUPLOGIN_SUCCESS,
      payload: res.data,
    });
    dispatch(loadUser());
  } catch (err) {
    console.log(err.response);
    dispatch({
      type: SIGNUPLOGIN_FAIL,
    });
  }
};

//==== signUp User
export const signUpUser =
  (name, email, password, confirmPassword) => async (dispatch) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const body = JSON.stringify({ name, email, password, confirmPassword });

    try {
      const res = await axios.post(
        "http://localhost:5000/api/v1/users/signup",
        body,
        config
      );
      dispatch({
        type: SIGNUPLOGIN_SUCCESS,
        payload: res.data,
      });
      dispatch(loadUser());
    } catch (err) {
      console.log(err.response);
      dispatch({
        type: SIGNUPLOGIN_FAIL,
      });
    }
  };

//==== sign Out and destroy the token
export const signOut = () => (dispatch) => {
  dispatch({
    type: USER_LOGOUT,
  });
};
